import React, { useEffect } from 'react';
import './App.css';
import Header from './Header';
import Home from './Home';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Checkout from './Checkout';
import Login from './Login';
import Payment from './Payment';
import { auth } from './firebase';
import { useStateValue } from "./StateProvider";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";


const promise = loadStripe(
  "pk_test_51JnSDhSDfKBGEb28G6LJ4E1qwyV2j3TuLvh6nmI6GhfQvYo5z5Su0zpoZrPAZAQrNM9xMmA2nBBL1HZOGB7v6Sqb00uK37SbKQ"
  );



function App() {

  const [{}, dispatch] = useStateValue();

  useEffect(() => {
    //will only run once when the app component loads....(its like an if statement in react)

    auth.onAuthStateChanged(authUser => {
      console.log('THE USER IS >>>> ', authUser);

      if (authUser){
        //the user was logged in r the user was logged in
        dispatch({
          type: 'SET_USER',
          user: authUser
        })

      } else{
        //the user is logged out
        dispatch({
          type: 'SET_USER',
          user: null
        })
      }
    })
  }, [])



  return (
    //it follows BEM convention
    <Router>
    <div className="app">
      
    
      <Switch> 
        <Route path="/login">
        <Login />
        </Route>

        <Route path="/checkout">
        <Header />
        <Checkout />
        </Route>

        <Route path="/payment">
        <Header />
        <Elements stripe={promise}>
        <Payment />
        </Elements>
        </Route>

        <Route path="/">
        <Header />
        <Home />
        </Route>
      </Switch>
      
    </div>
    </Router>
    
  );
}

export default App;